import { Container, Box, CssBaseline, useMediaQuery, useTheme } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Header from '../components/Header';
import Footer from '../components/Footer';
import HeroAccount from '../components/Account/HeroAccount';
import DashAccount from '../components/Account/DashAccount';
import ButtonAccount from '../components/Account/ButtonAccount';

const theme1 = createTheme({
  palette: {
    primary: {
      main: '#BE3144', // Example primary color, adjust as needed
    },
    secondary: {
      main: '#1E1E1E', // Dark background color
    },
    background: {
      default: '#1E1E1E',
      paper: 'white', // Darker paper color for cards
    },
    text: {
      primary: '#FFFFFF', // White text color
      secondary: '#BDBDBD', // Grayish secondary text color
    },
  },
  typography: {
    fontFamily: 'Roboto, sans-serif',
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          borderRadius: '12px',
          padding: '12px 24px',
          fontWeight: 'bold',
        },
      },
    },
    MuiCard: {
      styleOverrides: {
        root: {
          backgroundColor: '#333333',
          color: '#FFFFFF',
        },
      },
    },
  },
});

function Account() {
  return (
    <ThemeProvider theme={theme1}>
      <Box sx={{ background: 'linear-gradient(90deg, rgba(142,38,63,1) 0%, rgba(37,32,33,1) 100%)' }} color="white">
        <CssBaseline />
        <Container maxWidth="lg" sx={{ py: 4 }}>
          <Header user="ki.fg.wam" />
          <HeroAccount />
          <DashAccount />
          <ButtonAccount />
        </Container>
      </Box>
      <Footer />
    </ThemeProvider >
  );
}

export default Account;
