import React from "react";
import { Box, Typography, Grid, Paper, Avatar, useMediaQuery } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";

const theme = createTheme({
  palette: {
    background: {
      default: "tran",
    },
    text: {
      primary: "#ffffff",
    },
  },
  typography: {
    fontFamily: "Arial, sans-serif",
  },
});

const HeroAccount: React.FC = () => {
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          backgroundColor: theme.palette.background.default,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          padding: isMobile ? 2 : 4,
        }}
      >
        <Grid container alignItems="center" justifyContent="space-between">
          {/* Avatar and Account Name */}
          <Grid item xs={12} sm={6} md={4}>
            <Box display="flex" alignItems="center">
              <Box
                sx={{
                  width: isMobile ? 80 : 170,
                  height: isMobile ? 80 : 170,
                  backgroundColor: "#8C243F",
                  marginRight: isMobile ? 1 : 2,
                  marginBottom: isMobile ? 1 : 2,
                  borderRadius: "50%",
                  boxShadow: "5px 5px 15px 2px rgba(30, 30, 30, 0.3)",
                }}
              ></Box>
              <Typography
                variant={isMobile ? "h6" : "h4"}
                sx={{ color: theme.palette.text.primary, marginLeft: 2 }}
              >
                ki.fg.wam
              </Typography>
            </Box>
          </Grid>

          {/* Balance Cards */}
          <Grid item xs={12} sm={6} md={4}>
            <Grid container spacing={2}>
              {/* Total WAX Balance */}
              <Grid item xs={12} sm={6}>
                <Paper
                  elevation={3}
                  sx={{
                    padding: isMobile ? 1 : 2,
                    borderRadius: "16px",
                    backgroundColor: "#391E24",
                    color: theme.palette.text.primary,
                    textAlign: "center",
                  }}
                >
                  <Typography variant="body1" sx={{ fontSize: 22, textAlign: 'left' }}>Total WAX <br /> Balance:</Typography>
                  <Typography variant="h6" sx={{ fontSize: 22 }}>6.12354672</Typography>
                </Paper>
              </Grid>

              {/* Total USD Balance */}
              <Grid item xs={12} sm={6}>
                <Paper
                  elevation={3}
                  sx={{
                    padding: isMobile ? 1 : 2,
                    borderRadius: "16px",
                    backgroundColor: "#391E24",
                    color: theme.palette.text.primary,
                    textAlign: "center",
                  }}
                >
                  <Typography variant="body1" sx={{ fontSize: 22, textAlign: 'left' }}>Total USD <br /> Balance:</Typography>
                  <Typography variant="h6" sx={{ fontSize: 22 }}>$1.9321245</Typography>
                </Paper>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </ThemeProvider>
  );
};

export default HeroAccount;
