import React, { useState } from 'react';
import {
  Box,
  Button,
  Container,
  Typography,
  useMediaQuery,
  useTheme,
  styled,
  TextField,
  InputLabel
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
const StyledButton = styled(Button)({
  backgroundColor: 'transparent',
  border: '1px dashed white',
  borderRadius: 0,
  '&:hover': {
    backgroundColor: '#C1668B',
  },
});

const StyledButton2 = styled(Button)({
  backgroundColor: '#882140',
  border: '1px solid #bbb',
  borderRadius: 0,
  color: 'white',
  padding: '7px 22px',
  '&:hover': {
    backgroundColor: '#C1668B',
  },
});

const StyledButton3 = styled(Button)({
  backgroundColor: '#882140',
  borderRadius: 0,
  border: 'none',
  boxShadow: 'none',
  color: 'white',
  padding: '7px 22px',
  '&:hover': {
    backgroundColor: '#C1668B',
  },
});

interface FormProps {
  isSmallScreen: boolean;
  handleToggle: () => void;
  formStyle: Record<string, any>;
  inputStyle: Record<string, any>;
  labelStyle: Record<string, any>;
}

const LimitlessWAXForm = ({ isSmallScreen, handleToggle, formStyle, inputStyle, labelStyle }: FormProps) => (
  <Box sx={formStyle}>
    <Container maxWidth="lg">
      <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant={isSmallScreen ? "h6" : "h4"} component="h1" gutterBottom>
          LimitlessWAX
        </Typography>

        <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 1 }}>
          <StyledButton3 color="inherit" onClick={(handleToggle)}>Save</StyledButton3>
          <StyledButton3 color="inherit">Load</StyledButton3>
        </Box>
      </Box>

      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        <Box sx={{ width: { xs: '100%', sm: '50%' } }}>
          <InputLabel sx={labelStyle}>Name this action</InputLabel>
          <TextField variant="outlined" fullWidth sx={inputStyle} />
        </Box>

        <Box sx={{ width: { xs: '100%', sm: '50%' } }}>
          <InputLabel sx={labelStyle}>Smart contract address</InputLabel>
          <TextField variant="outlined" fullWidth sx={inputStyle} />
        </Box>

        {Array.from({ length: 4 }).map((_, index) => (
          <Box key={index} sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 2 }}>
            <Box sx={{ flex: 1 }}>
              <InputLabel sx={labelStyle}>Action #{index + 1}</InputLabel>
              <TextField variant="outlined" fullWidth sx={inputStyle} />
            </Box>
            <Box sx={{ flex: 1 }}>
              <InputLabel sx={labelStyle}>Data #{index + 1}</InputLabel>
              <TextField variant="outlined" fullWidth sx={inputStyle} />
            </Box>
          </Box>
        ))}

        <Box sx={{ width: '100%' }}>
          <StyledButton variant="outlined" color="inherit">Add Action +</StyledButton>
        </Box>

        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 5 }}>
          <Box>
            <InputLabel sx={labelStyle}>How much CPU</InputLabel>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <TextField variant="outlined" sx={{ ...inputStyle, width: 70 }} />
              <InputLabel sx={labelStyle}>ms</InputLabel>
            </Box>
          </Box>
          <Box>
            <InputLabel sx={labelStyle}>Pay with</InputLabel>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <StyledButton3 variant="contained" color="primary">WAX</StyledButton3>
              <Typography variant="body1">or</Typography>
              <StyledButton variant="outlined" color="inherit">Custom Token +</StyledButton>
            </Box>
          </Box>
        </Box>

        <Box sx={{ display: 'flex', width: '100%', gap: 2, mt: 3, mb: 8 }}>
          <StyledButton2 variant="contained" color="primary">Submit</StyledButton2>
          <StyledButton2 variant="outlined" color="inherit">Reset</StyledButton2>
        </Box>
      </Box>
    </Container>
  </Box>
);

const LimitlessWAX: React.FC = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const [toggle, setToggle] = useState(false);
  const handleToggle = () => setToggle(!toggle);

  const formStyle = {
    backgroundColor: '#4A1E2A', // Maroon color
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    color: 'white',
    padding: theme.spacing(3),
  };

  const inputStyle = {
    backgroundColor: '#831F3F',
    color: 'white',
    '& .MuiInputBase-input': {
      color: 'white',
      padding: '10px 20px',
    },
    '& .MuiInputLabel-root': {
      color: 'white',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: 'white',
        border: 'none',
      },
      '&:hover fieldset': {
        borderColor: 'white',
        border: 'none',
      },
      '&.Mui-focused fieldset': {
        borderColor: 'white',
        border: 'none',
      },
    },
  };

  const labelStyle = {
    color: 'white',
    marginBottom: 1,
    fontWeight: 500,
    fontSize: { xs: '1rem', sm: '1.2rem' }
  };

  return (
    <>
      {toggle ? (
        <LimitlessWAXForm
          isSmallScreen={isSmallScreen}
          handleToggle={handleToggle}
          formStyle={formStyle}
          inputStyle={inputStyle}
          labelStyle={labelStyle}
        />
      ) : (
        <Box
          sx={{
            backgroundColor: '#4A1E2A', // Maroon color
            color: 'white',
            marginBottom: 5
          }}
        >
          <Typography variant={isSmallScreen ? "h6" : "h4"} component="h1" gutterBottom ml={3} pt={3}>
            LimitlessWAX
          </Typography>
          <Container maxWidth="sm">
            <Box sx={{ textAlign: 'center', padding: 3, mt: { xs: 5, sm: 10, md: 15 } }}>

              <Box sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                gap: 2,
                flexDirection: isSmallScreen ? 'column' : 'row', // Stack on small screens
                marginBottom: 3
              }}>
                <StyledButton variant="contained" color="primary" onClick={handleToggle}>
                  Add Action +
                </StyledButton>
                <Typography variant="body1">or</Typography>
                <StyledButton2 variant="contained" color="secondary">
                  Load
                </StyledButton2>
              </Box>

              <Box
                sx={{
                  display: 'flex',
                  flexWrap: 'wrap',
                  gap: 2,
                  justifyContent: 'center',
                  alignItems: 'center',
                  mt: { xs: 5, sm: 10, md: 15 },
                  backgroundColor: '#882140',
                  p: 2,
                  borderRadius: 3
                }}
              >
                {Array.from({ length: 4 }).map((_, index) => (
                  <Box key={index}>
                    <StyledButton
                      variant="contained"
                      color="primary"
                      fullWidth
                      sx={{
                        '&:hover': {
                          bgcolor: '#C1668B',
                        },
                      }}
                    >
                      <AddIcon sx={{ fontSize: '2rem', fontWeight: '900' }} />
                    </StyledButton>
                  </Box>
                ))}
              </Box>
            </Box>
          </Container>
        </Box>
      )}
    </>
  );
};

export default LimitlessWAX;
